export default {
	data() {
		return {
			btnIsDisable: true,
			oldPwd: "",
			newPwd: "",
			newConfirmPwd: "",
			loading: false
		}
	},
	watch: {
		oldPwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		},
		newPwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		},
		newConfirmPwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		}
	},
	methods: {
		handleConfirm() {
			if (!this.inputIsEmpty() && this.newPwd == this.newConfirmPwd) {
				this.loading = true
				this.btnIsDisable = true
				uni.showToast({
					title: "修改成功"
				})
			} else {
				uni.showToast({
					title: "两次新密码不一致",
					icon: "none"
				})
			}
		},
		inputIsEmpty() {

			if (this.oldPwd && this.oldPwd != null && this.newPwd && this.newPwd != null && this.newConfirmPwd && this.newConfirmPwd !=
				null) {
				return false;
			}
			return true;
		}
	}
}
